"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useAuth } from "@/contexts/AuthContext"
import Link from "next/link"
import { getSubjects, createSubject, deleteSubject, getSemester } from "@/lib/firestore"
import type { Subject, Semester } from "@/types"

export default function SubjectsPage({ params }: { params: { semesterId: string } }) {
  const { user } = useAuth()
  const [subjects, setSubjects] = useState<Subject[]>([])
  const [semester, setSemester] = useState<Semester | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  // Form state
  const [showForm, setShowForm] = useState(false)
  const [formData, setFormData] = useState({
    code: "",
    name: "",
    difficulty: "Easy" as "Easy" | "Medium" | "Hard"
  })

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [semesterData, subjectsData] = await Promise.all([
          getSemester(params.semesterId),
          getSubjects(params.semesterId)
        ])
        setSemester(semesterData)
        setSubjects(subjectsData)
      } catch (error) {
        console.error("Error fetching data:", error)
        setError("Failed to load data")
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [params.semesterId])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      await createSubject({
        ...formData,
        semesterId: params.semesterId,
        chapters: []
      })
      const updatedSubjects = await getSubjects(params.semesterId)
      setSubjects(updatedSubjects)
      setShowForm(false)
      setFormData({ code: "", name: "", difficulty: "Easy" })
    } catch (error) {
      console.error("Error creating subject:", error)
      setError("Failed to create subject")
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (subjectId: string) => {
    if (!confirm("Are you sure? This will delete all chapters and quizzes in this subject.")) {
      return
    }
    setLoading(true)
    try {
      await deleteSubject(subjectId)
      const updatedSubjects = await getSubjects(params.semesterId)
      setSubjects(updatedSubjects)
    } catch (error) {
      console.error("Error deleting subject:", error)
      setError("Failed to delete subject")
    } finally {
      setLoading(false)
    }
  }

  // Only allow admin access
  if (user?.email !== 'jeniperwalk@gmail.com') {
    return (
      <main className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <h1 className="text-2xl font-bold text-gray-800 mb-4">Access Denied</h1>
          <p className="text-gray-600 mb-4">You do not have permission to access this page.</p>
          <Link href="/">
            <Button className="bg-orange-500">Back to Dashboard</Button>
          </Link>
        </Card>
      </main>
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    )
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <div>
            <Link 
              href="/admin"
              className="text-orange-500 hover:text-orange-600 mb-2 inline-flex items-center gap-2"
            >
              ← Back to Admin
            </Link>
            <h1 className="text-2xl font-bold text-gray-800">
              Manage Subjects - Semester {semester?.number}
            </h1>
          </div>
          <Button 
            onClick={() => setShowForm(true)}
            className="bg-green-600 hover:bg-green-700"
            disabled={loading}
          >
            Add Subject
          </Button>
        </div>

        {error && (
          <div className="bg-red-50 text-red-500 p-4 rounded-lg mb-6">
            {error}
          </div>
        )}

        {showForm && (
          <Card className="p-6 mb-8">
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Subject Code
                </label>
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => setFormData({ ...formData, code: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Subject Name
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Difficulty
                </label>
                <select
                  value={formData.difficulty}
                  onChange={(e) => setFormData({ ...formData, difficulty: e.target.value as "Easy" | "Medium" | "Hard" })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                >
                  <option value="Easy">Easy</option>
                  <option value="Medium">Medium</option>
                  <option value="Hard">Hard</option>
                </select>
              </div>
              <div className="flex gap-4">
                <Button 
                  type="submit"
                  className="flex-1 bg-orange-500"
                  disabled={loading}
                >
                  Create Subject
                </Button>
                <Button 
                  type="button"
                  onClick={() => setShowForm(false)}
                  className="bg-gray-200 text-gray-700 hover:bg-gray-300"
                  disabled={loading}
                >
                  Cancel
                </Button>
              </div>
            </form>
          </Card>
        )}

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {subjects.map((subject) => (
            <Card key={subject.id} className="p-6">
              <div className="flex justify-between items-start mb-4">
                <div>
                  <h2 className="text-xl font-semibold text-gray-800">
                    {subject.name}
                  </h2>
                  <p className="text-gray-600">Code: {subject.code}</p>
                  <p className="text-gray-600">Difficulty: {subject.difficulty}</p>
                </div>
                <Button
                  onClick={() => handleDelete(subject.id)}
                  variant="destructive"
                  size="sm"
                  className="bg-red-500 hover:bg-red-600"
                >
                  Delete
                </Button>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </main>
  )
}
